import PropTypes from 'prop-types';

const ButtonVariants = Object.freeze({
  PRIMARY: 'primary',
  SECONDARY: 'secondary',
  TERTIARY: 'tertiary',
  TERTIARY_INVERSE: 'tertiary_inverse',
  TERTIARY_OFF: 'tertiary_off',
  TERTIARY_ON: 'tertiary_on',
  ON: 'on',
  OFF: 'off',
  DANGER_PRIMARY: 'danger_primary',
  DANGER_SECONDARY: 'danger_secondary',
});

const IconButtonVariants = Object.freeze({
  PRIMARY: 'primary',
  SECONDARY: 'secondary',
  ON: 'on',
  OFF: 'off',
  INVERSE: 'inverse',
});

const ButtonSizes = Object.freeze({
  SMALL: 'small',
  MEDIUM: 'medium',
});

const ButtonVariantPropType = PropTypes.oneOf(Object.values(ButtonVariants));
const IconButtonVariantPropType = PropTypes.oneOf(Object.values(IconButtonVariants));
const ButtonSizePropType = PropTypes.oneOf(Object.values(ButtonSizes));

export {
  ButtonVariants,
  ButtonSizes,
  ButtonSizePropType,
  IconButtonVariants,
  IconButtonVariantPropType,
};
export default ButtonVariantPropType;
