import React from 'react';
import styled from 'styled-components';

import {
  AppBarColors,
} from './proptypes';

const getBorder = (color, underline) => underline ? `1px solid ${color}` : 'none';

/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
const HeaderWithoutSomeProps = React.forwardRef(
  ({
    position, theme, color, ...props
  }, ref) => <header ref={ref} {...props} />
);

const Header = styled(HeaderWithoutSomeProps)`
  width: 100%;
  display: block;
  position: ${({ position }) => position};
  top: 0;
  left: 0;
  right: 0;
  z-index: ${({ theme }) => theme.zIndex.appBar};
  background: ${({ theme }) => theme.palette.common.white};

  ${({ underline, theme }) => `
    border-bottom: ${getBorder(theme.palette.grey.lighter, underline)};
  `}

  ${({ theme, color, underline }) => color === AppBarColors.TRANSPARENT && `
    background: ${theme.palette.common.transparent};
    border-bottom: ${getBorder(theme.palette.common.transparent, underline)};
  `}
`;

export default Header;
