import getDefaultErrors from './getDefaultErrors';
import { requiredRuleName, trimRuleName } from './defaultRuleNames';

export const requiredValidator = (intl, { requiredError } = getDefaultErrors(intl)) => ({
  reqValidators: [requiredRuleName, trimRuleName],
  reqErrorMessages: [requiredError, requiredError],
});

export default requiredValidator;
