import PropTypes from 'prop-types';

const LinkTarget = Object.freeze({
  BLANK: '_blank',
  PARENT: '_parent',
  SELF: '_self',
  TOP: '_top',
});

const LinkVariants = Object.freeze({
  DEFAULT: 'default',
  INVERSE: 'inverse',
});

const LinkVariantPropType = PropTypes.oneOf(Object.values(LinkVariants));


const LinkRel = Object.freeze({
  ALTERNATE: 'alternate',
  AUTHOR: 'author',
  BOOKMARK: 'bookmark',
  EXTERNAL: 'external',
  HELP: 'help',
  LICENSE: 'license',
  NEXT: 'next',
  NOFOLLOW: 'nofollow',
  NOREFERRER: 'noreferrer',
  NOOPENER: 'noopener',
  PREV: 'prev',
  SEARCH: 'search',
  TAG: 'tag',
});

const LinkTargetPropType = PropTypes.oneOf(Object.values(LinkTarget));
const LinkRelPropType = PropTypes.oneOf(Object.values(LinkRel));

export {
  LinkTarget,
  LinkRel,
  LinkVariants,
  LinkTargetPropType,
  LinkRelPropType,
  LinkVariantPropType,
};
export default LinkTargetPropType;
