const shorthandRegex = /^#?([a-f\d])([a-f\d])([a-f\d])$/i; // i.e.: #f0a
const fullformRegex = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i; // i.e.: #f0f0f0
const rgbRegex = /(\d+), *(\d+), *(\d+)/;

const resultToArray = (result, radix = 10) => result
  ? [parseInt(result[1], radix), parseInt(result[2], radix), parseInt(result[3], radix)]
  : [0, 0, 0];

export const colorToRgbArray = (hex) => {
  const rgbResult = rgbRegex.exec(hex);
  if (rgbResult) {
    // Fallback for rgb colors
    return resultToArray(rgbResult, 10);
  }

  // Expand shorthand form (e.g. "03F") to full form (e.g. "0033FF")
  const newHex = hex.replace(shorthandRegex, (m, r, g, b) => r + r + g + g + b + b);

  const result = fullformRegex.exec(newHex);

  return resultToArray(result, 16);
};

// eslint-disable-next-line max-len
export const colorToRgbString = (hex, opacity) => `rgba(${colorToRgbArray(hex).join(',')},${typeof opacity !== 'number' ? '1' : opacity})`;
