import React from 'react';
import { FormattedMessage } from 'react-intl';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import withTheme from '@quintoandar/cozy-utils/hocs/withTheme';
import Typography, { variantType } from '../Typography';

import ThemePropType from '../styles/Theme/proptypes';

export const Title = styled(({ gutter, theme, ...props }) => <Typography {...props} />)`
  padding-left: ${({ gutter, theme }) => gutter ? theme.spacing(9) : theme.spacing(2)}px;
`;

export const AppBarTitle = ({
  title, component, gutter, theme,
}) => (
  <Title
    theme={theme}
    variant={variantType.title}
    component={component}
    gutterBottom={gutter}
    gutter={gutter}
    inline
  >
    <FormattedMessage {...title} />
  </Title>
);

AppBarTitle.propTypes = {
  theme: ThemePropType,
  title: PropTypes.object,
  component: PropTypes.string,
  gutter: PropTypes.bool,
};

AppBarTitle.defaultProps = {
  component: 'h1',
  gutter: false,
};

export default withTheme(AppBarTitle);
