import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { sizing } from '@material-ui/system';
import IconButton from '@material-ui/core/IconButton';

import withTheme from '@quintoandar/cozy-utils/hocs/withTheme';
import IconButtonVariantPropType, { IconButtonVariants } from '../proptypes';

import { propTypes } from '../ButtonBase';
import buttonStyles from './buttonStyles';

export const disabled = 'disabled';
// eslint-disable-next-line no-unused-vars
export const IconButtonBase = styled(({ variant, theme, ...rest }) =>
  (
    <IconButton
      {...rest}
      variant={variant === IconButtonVariants.PRIMARY ? 'contained' : 'text'}
    />
  )).attrs({ classes: { disabled } })`
    ${sizing};
    ${({ variant, theme }) => buttonStyles(theme)[variant]};

    &.${disabled} {
      background-color: ${({ theme }) => theme.palette.grey.lighter};
      color: ${({ theme }) => theme.palette.grey.disabled};

      svg {
        color: ${({ theme }) => theme.palette.grey.disabled};
      }
    }

    ${({ size }) => size === 'small' && `
      width: 48px;
      height: 48px;

      svg {
        width: 16px;
        height: 16px;
      }
    `}
`;

export const Icon = ({ children, ariaLabel, ...rest }) => (
  <IconButtonBase color="primary" aria-label={ariaLabel} {...rest}>
    {children}
  </IconButtonBase>
);

Icon.propTypes = {
  ...propTypes,
  ariaLabel: PropTypes.string,
  size: PropTypes.string,
  variant: IconButtonVariantPropType,
};

Icon.defaultProps = {
  variant: IconButtonVariants.PRIMARY,
  size: 'medium',
};

export { IconButtonVariants };
export default withTheme(Icon);
