import React from 'react';

import theme from '../styles/Theme';

import StyledTypography from './StyledTypography';
import { variantType as variants } from './variantType';
import TypographyPropType from './proptypes';

export function getColor(nativecolor, color) {
  if (nativecolor) {
    return nativecolor;
  }

  const colors = {
    inherit: 'inherit',
    primary: theme.palette.primary.main,
    secondary: theme.palette.secondary.main,
    textPrimary: theme.palette.grey.highEmphasis,
    textSecondary: theme.palette.text.secondary,
    success: theme.palette.success.main,
    error: theme.palette.error.main,
    disabled: theme.palette.grey.disabled,
    default: theme.palette.grey.highEmphasis,
  };

  return colors[color];
}

export const variantType = variants;

export const Typography = ({
  className,
  inline,
  component,
  variant,
  align,
  gutterBottom,
  noWrap,
  nativecolor,
  color,
  bold,
  children,
  ...rest
}) => (
  <StyledTypography
    className={className}
    inline={inline}
    component={component}
    variant={variant}
    align={align}
    gutterBottom={gutterBottom}
    theme={theme}
    nowrap={noWrap}
    color={getColor(nativecolor, color)}
    bold={bold}
    {...rest}
  >
    {children}
  </StyledTypography>
);

Typography.propTypes = TypographyPropType;

Typography.defaultProps = {
  variant: variants.body1,
  component: 'span',
  gutterBottom: false,
  inline: false,
  noWrap: false,
  align: 'inherit',
  color: 'inherit',
};

export default Typography;
