import PropTypes from 'prop-types';

const ColorType = Object.freeze({
  PRIMARY: 'primary',
  SECONDARY: 'secondary',
  TEXTPRIMARY: 'textPrimary',
  TEXTSECONDARY: 'textSecondary',
  SUCCESS: 'success',
  ERROR: 'error',
  INHERIT: 'inherit',
  DEFAULT: 'default',
  DISABLED: 'disabled',
});

const AlignType = Object.freeze({
  INHERIT: 'inherit',
  LEFT: 'left',
  CENTER: 'center',
  RIGHT: 'right',
  JUSTIFY: 'justify',
});

const VariantType = Object.freeze({
  DISPLAY1: 'display1',
  HEADLINE: 'headline',
  TITLE: 'title',
  SUBHEADING: 'subheading',
  BODY2: 'body2',
  BODY1: 'body1',
  CAPTION: 'caption',
  BUTTON: 'button',
});

export const VariantPropType = PropTypes.oneOf(Object.values(VariantType));
export const AlignPropType = PropTypes.oneOf(Object.values(AlignType));
export const ColorPropType = PropTypes.oneOf(Object.values(ColorType));

const TypographyPropType = {
  className: PropTypes.string,
  variant: VariantPropType,
  gutterBottom: PropTypes.bool,
  inline: PropTypes.bool,
  nativecolor: PropTypes.string,
  component: PropTypes.oneOfType([PropTypes.string, PropTypes.func, PropTypes.object]),
  children: PropTypes.node.isRequired,
  align: AlignPropType,
  noWrap: PropTypes.bool,
  bold: PropTypes.bool,
  color: ColorPropType,
};

export default TypographyPropType;
