import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import MuiBox from '@material-ui/core/Box';

import withTheme from '@quintoandar/cozy-utils/hocs/withTheme';
import ThemePropType from '../styles/Theme/proptypes';

/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
const MuiBoxWithoutSomeProps = React.forwardRef(({ theme, gridgap, ...props }, ref) =>
  <MuiBox ref={ref} {...props} />);

export const Box = styled(MuiBoxWithoutSomeProps)`
  grid-gap: ${({ gridgap, theme }) => theme.spacing(gridgap)}px;
`;

Box.propTypes = {
  gridgap: PropTypes.number,
  theme: ThemePropType.isRequired,
};

Box.defaultProps = {
  gridgap: 0,
};

export default withTheme(Box);
