import PropTypes from 'prop-types';

const AlertColors = Object.freeze({
  SUCCESS: 'success',
  ERROR: 'error',
  ATTENTION: 'attention',
  GREY: 'grey',
  PRIMARY: 'primary',
});

const AlertColorPropType = PropTypes.oneOf(Object.values(AlertColors));

export { AlertColors };
export default AlertColorPropType;
