import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import MuiTooltip from '@material-ui/core/Tooltip';
import withTheme from '@quintoandar/cozy-utils/hocs/withTheme';
import PlacementPropType from '../proptypes/Placements';

const tooltip = 'tooltip';

// eslint-disable-next-line no-unused-vars
export const Tooltip = styled(({ highlightBackground, ...props }) => (
  <MuiTooltip
    classes={{
      popper: props.className,
      tooltip,
    }}
    {...props}
  />
))`
  & .${tooltip} {
    background-color: ${({ highlightBackground, theme }) => highlightBackground ? theme.palette.primary.main : ''};
    border-radius: ${({ theme }) => theme.spacing(0.5)}px;
    margin: ${({ theme, arrow }) => arrow ? '' : `${theme.spacing(0)}px`};
  }
`;

Tooltip.propTypes = {
  title: PropTypes.node.isRequired,
  placement: PlacementPropType,
  highlightBackground: PropTypes.bool,
  enterDelay: PropTypes.number,
};

Tooltip.defaultProps = {
  highlightBackground: true,
  enterDelay: 100,
};

export default withTheme(Tooltip);
