import React from 'react';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';

import Typography, { variantType } from '../../Typography';
import MessageDescriptorPropType from '../../proptypes/Intl';

const AdornmentText = ({ message }) => (
  <Typography color="textSecondary" variant={variantType.caption}>
    {message.defaultMessage
      ? <FormattedMessage {...message} />
      : message}
  </Typography>
);

AdornmentText.propTypes = {
  message: PropTypes.oneOfType([
    PropTypes.string,
    MessageDescriptorPropType,
  ]),
};

export default AdornmentText;
