import PropTypes from 'prop-types';

import { materialBreakpoints } from '../breakpoints';
import { variantType } from '../../Typography';

export const WidthPropType = {
  all: PropTypes.oneOf(materialBreakpoints.keys),
};

const PalettePropType = PropTypes.shape({
  light: PropTypes.string,
  main: PropTypes.string,
  dark: PropTypes.string,
  contrastText: PropTypes.string,
  contrastDefaultColor: PropTypes.string,
});

const GreyPalettePropType = PropTypes.shape({
  highEmphasis: PropTypes.string,
  mediumEmphasis: PropTypes.string,
  disabled: PropTypes.string,
  lighter: PropTypes.string,
  background: PropTypes.string,
});

const CustomPalettePropType = PropTypes.shape({
  light: PropTypes.string,
  main: PropTypes.string,
  contrastText: PropTypes.string,
  contrastDefaultColor: PropTypes.string,
});

export const ThemePropType = PropTypes.shape({
  palette: PropTypes.shape({
    primary: PalettePropType,
    secondary: PalettePropType,
    grey: GreyPalettePropType,
    error: CustomPalettePropType,
    success: CustomPalettePropType,
    attention: CustomPalettePropType,
    common: PropTypes.shape({
      white: PropTypes.string,
      black: PropTypes.string,
      transparent: PropTypes.string,
    }),
  }),
});

export const VariantPropType = PropTypes.oneOf(Object.values(variantType));

export const ColorPropType = {
  all: PropTypes.oneOf(['primary', 'secondary', 'inherit', 'default']),
  theme: PropTypes.oneOf(['primary', 'secondary']),
};

export const TypographyColorPropType = {
  all: PropTypes.oneOf([
    'default',
    'disabled',
    'error',
    'inherit',
    'primary',
    'secondary',
    'textPrimary',
    'textSecondary',
  ]),
};

export const IconPropType = PropTypes.oneOf([
  'inherit',
  'primary',
  'secondary',
  'action',
  'error',
  'disabled',
]);


export default ThemePropType;
