import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

import withTheme from '@quintoandar/cozy-utils/hocs/withTheme';
import ThemePropType from '../styles/Theme/proptypes';
import { variantTypeStyles } from '../Typography/variantType';

export const AppBarLink = styled(({
  component: Link, to, target, children, ...rest
}) =>
  Link
    ? <Link to={to} {...rest}>{children}</Link>
    : <a href={to} target={target} {...rest}>{children}</a>)`
    ${variantTypeStyles(false).body2}
    text-decoration: none;
    cursor: pointer;
    margin: 0 ${({ theme }) => theme.spacing(3)}px;
    color: ${({ linkColor, theme }) => linkColor || theme.palette.grey.highEmphasis};

    &:hover {
      color: ${({ theme, hoverColor }) => hoverColor || theme.palette.primary.main};
    }
`;

AppBarLink.propTypes = {
  theme: ThemePropType,
  to: PropTypes.string,
  target: PropTypes.oneOf(['_blank', '_self', 'parent', '_top']),
  children: PropTypes.any,
};

AppBarLink.defaultProps = {
  target: '_self',
};

export default withTheme(AppBarLink);
