import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { injectIntl } from 'react-intl';

import { MessageDescriptorPropType } from '../proptypes/Intl';
import InputVariantPropType, { InputVariants } from '../Input/proptypes';
import FieldWrapper from './Wrapper';
import Description from './Description';
import messages from './messages';

export const Field = ({
  intl,
  children,
  helperText,
  label,
  variant,
  onInfoClick,
  infoIcon,
  infoIconAriaLabel,
  labelFor,
  tooltipLabel,
  gutterBottom,
}) => (
  <Fragment>
    <FieldWrapper htmlFor={labelFor}>
      <Description
        label={label}
        helperText={helperText}
        variant={variant}
        onInfoClick={onInfoClick}
        infoIcon={infoIcon}
        infoIconAriaLabel={infoIconAriaLabel || intl.formatMessage(messages.infoIcon)}
        tooltipLabel={tooltipLabel}
        gutterBottom={gutterBottom}
      />
    </FieldWrapper>
    {children}
  </Fragment>
);

Field.propTypes = {
  intl: PropTypes.object.isRequired,
  children: PropTypes.oneOfType([PropTypes.element, PropTypes.node]),
  label: MessageDescriptorPropType,
  helperText: MessageDescriptorPropType,
  variant: InputVariantPropType,
  infoIcon: PropTypes.bool,
  onInfoClick: PropTypes.func,
  infoIconAriaLabel: PropTypes.string,
  tooltipLabel: PropTypes.oneOfType([PropTypes.string, MessageDescriptorPropType]),
  labelFor: PropTypes.string,
  gutterBottom: PropTypes.bool,
};

Field.defaultProps = {
  infoIcon: false,
  variant: InputVariants.DEFAULT,
  gutterBottom: false,
};

export default injectIntl(Field);
