import styled, { css } from 'styled-components';

import withTheme from '@quintoandar/cozy-utils/hocs/withTheme';

const Adornment = styled.div`
  min-width: ${({ endAdornment }) => endAdornment ? '48px' : 'auto'};
  padding: ${({ select, theme }) =>
    select ? `0 ${theme.spacing()}px 0 ${theme.spacing(0.5)}px` : `0 ${theme.spacing()}px`};
  color: ${({ theme }) => theme.palette.grey.mediumEmphasis};
  display: flex;
  justify-content: ${({ endAdornment }) => endAdornment ? 'flex-end' : 'flex-start'};

  ${({ endAdornment, multiline, theme }) => (endAdornment && multiline) && css`
    position: absolute;
    bottom: ${theme.spacing()}px;
    right: ${theme.spacing()}px;
  `}
`;

export default withTheme(Adornment);
