import { css } from 'styled-components';
import { InputVariants } from './proptypes';

const ValidatorStyles = (theme, disabled, error, valid, blur, focus) => ({
  default: css`
    > [class*="MuiInputBase-root"] {
      border-radius: ${theme.shape.borderRadius}px;
      margin: ${theme.spacing()}px 0;
      padding: 0 ${theme.spacing()}px;
      border: 1px solid ${theme.palette.grey.disabled};
      background: ${theme.palette.common.white};

      ${disabled && css`
        background: ${theme.palette.grey.lighter};
        color: ${theme.palette.grey.disabled};
      `}

      ${blur && css`
        border: 1px solid ${theme.palette.grey.disabled};
        background: ${theme.palette.common.white};
      `}

      ${focus && css`
        color: ${theme.palette.common.highEmphasis};
        border: 1px solid ${theme.palette.primary.main};
      `}

      ${error && css`
        border: 1px solid ${theme.palette.error.main};
        background: ${theme.palette.error.light};
      `}

      ${valid && css`
        border: 1px solid ${theme.palette.success.main};
        background: ${theme.palette.success.light};
      `}
    }
    > [class*="MuiFormHelperText-root"][class*="Mui-error"] {
      margin-top: 0;

      ${(props) => props['data-variant'] === InputVariants.INVERSE && css`
        color: ${theme.palette.common.white};
      `}

      &::before {
        content: ' '
      }
    }

    div {
      &:after, &:before {
        display: none;
      }
    }
  `,
});

export default ValidatorStyles;
