import React, { Fragment } from 'react';
import { FormattedMessage } from 'react-intl';
import PropTypes from 'prop-types';

import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';

import { IconButtonVariants } from '../../Button/proptypes';
import { MessageDescriptorPropType } from '../../proptypes/Intl';
import InputVariantPropType, { InputVariants } from '../../Input/proptypes';
import Typography, { variantType } from '../../Typography';
import { getVariantColor } from '../../Input/utils';
import InfoIconButton from './InfoIconButton';
import Tooltip from '../../Tooltip';

const Description = ({
  label, helperText, tooltipLabel, variant, onInfoClick, infoIcon, infoIconAriaLabel, tooltipProps, gutterBottom,
}) => {
  const iconButton = (
    <InfoIconButton
      onClick={onInfoClick}
      ariaLabel={infoIconAriaLabel}
      variant={variant === InputVariants.INVERSE ? IconButtonVariants.INVERSE : IconButtonVariants.PRIMARY}
    >
      <InfoOutlinedIcon />
    </InfoIconButton>
  );

  return (
    <Fragment>
      {label && (
        <Typography
          variant={variantType.subheading}
          inline={infoIcon}
          bold
          component="span"
          color={getVariantColor('default', variant)}
          gutterBottom={gutterBottom}
        >
          <FormattedMessage {...label} />
        </Typography>
      )}

      {(infoIcon && onInfoClick && !tooltipLabel)
        && iconButton}

      {tooltipLabel
        && (
          <Tooltip
            highlightBackground={false}
            enterTouchDelay={50}
            title={(
              <Typography p={0.5}>
                {tooltipLabel.defaultMessage ? <FormattedMessage {...tooltipLabel} /> : tooltipLabel}
              </Typography>
            )}
            {...tooltipProps}
          >
            {iconButton}
          </Tooltip>
        )}

      {helperText && (
        <Typography
          variant={variantType.caption}
          color={getVariantColor('textSecondary', variant)}
        >
          <FormattedMessage {...helperText} />
        </Typography>
      )}
    </Fragment>
  );
};


Description.propTypes = {
  helperText: MessageDescriptorPropType,
  label: MessageDescriptorPropType,
  variant: InputVariantPropType,
  infoIcon: PropTypes.bool,
  onInfoClick: PropTypes.func,
  infoIconAriaLabel: PropTypes.string,
  tooltipLabel: PropTypes.oneOfType([PropTypes.string, MessageDescriptorPropType]),
  tooltipProps: PropTypes.object,
  gutterBottom: PropTypes.bool,
};

export default Description;
