import React from 'react';
import PropTypes from 'prop-types';
import { injectIntl } from 'react-intl';

import withTheme from '@quintoandar/cozy-utils/hocs/withTheme';

import InputVariantPropType, { InputVariants } from './proptypes';
import MessageDescriptorPropType from '../proptypes/Intl';
import Box from '../Box';

import requiredValidator from './validators/required';
import Adornment from './Adornment/index';
import AdornmentText from './Adornment/AdornmentText';
import Input from './Input';
import TextArea from './TextArea';
import TextValidator from './TextValidator';

export const inputPropTypes = {
  inputProps: PropTypes.object,
  intl: PropTypes.object.isRequired,
  autoFocus: PropTypes.bool,
  disabled: PropTypes.bool,
  multiline: PropTypes.bool,
  withCounter: PropTypes.bool,
  error: PropTypes.bool,
  fullWidth: PropTypes.bool,
  inputRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.object }),
  ]),

  onBlur: PropTypes.func,
  onChange: PropTypes.func,
  onFocus: PropTypes.func,
  onValidate: PropTypes.func,

  maxLength: PropTypes.number,
  minLength: PropTypes.number,
  name: PropTypes.string.isRequired,
  type: PropTypes.string,
  dataTestId: PropTypes.string,
  placeholder: PropTypes.oneOfType([PropTypes.string, MessageDescriptorPropType]),

  readOnly: PropTypes.bool,
  required: PropTypes.bool,
  valid: PropTypes.bool,

  endAdornment: PropTypes.object,
  startAdornment: PropTypes.object,
  theme: PropTypes.object,

  validators: PropTypes.array,
  errorMessages: PropTypes.array,
  value: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  variant: InputVariantPropType,
};

export class BaseInput extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      valid: undefined,
      error: false,
      focus: false,
      blur: false,
    };

    this.ref = React.createRef();

    const { multiline, maxLength, inputProps } = props;

    // Props that will be used to communicate with native HTML tag API
    // E.g.: <textarea maxlength="10" />
    // E.g.: <input type="number" step="0.5" />
    const htmlTagProps = {
      maxLength,
      ...inputProps,
    };

    this.inputComponent = multiline
      ? (otherProps) => <TextArea {...otherProps} {...htmlTagProps} multiline />
      : (otherProps) => <Input {...otherProps} inputProps={htmlTagProps} />;
  }

  handleValidatorChange = (valid) => {
    const { onValidate } = this.props;
    this.setState({ valid, error: !valid });
    if (onValidate) {
      onValidate(valid);
    }
  }

  handleFocus = () => {
    const { onFocus } = this.props;
    this.setState({ focus: true, blur: false });
    if (onFocus) {
      onFocus();
    }
  };

  handleBlur = (e) => {
    const { value, onBlur } = this.props;
    this.setState({ blur: true, focus: false });
    this.ref.current.validate(value);
    if (onBlur) {
      onBlur(e);
    }
  };

  buildEndAdornmnet = () => {
    const { value, multiline, withCounter, maxLength, endAdornment } = this.props;

    const valueExists = typeof value === 'number' || typeof value === 'string';

    if (valueExists && withCounter && maxLength) {
      return (
        <Adornment endAdornment multiline={multiline}>
          <Box width="50px" display="flex" justifyContent="flex-end">
            <AdornmentText message={`${value.length}/${maxLength}`} />
          </Box>
        </Adornment>
      );
    }

    if (endAdornment) {
      return (
        <Adornment endAdornment>
          {endAdornment}
        </Adornment>
      );
    }

    return null;
  };

  render() {
    const {
      intl,
      autoFocus,
      disabled,
      multiline,
      endAdornment,
      fullWidth,
      dataTestId,
      name,
      placeholder,
      startAdornment,
      minLength,
      maxLength,
      withCounter,
      theme,
      variant,
      required,
      validators,
      errorMessages,
      InputProps,
      ...props
    } = this.props;

    const DefaultInputProps = {
      autoFocus,
      placeholder: placeholder && placeholder.defaultMessage ? intl.formatMessage(placeholder) : placeholder,
      disabled,
      maxLength,
      minLength,
      fullWidth,
      isRequired: required,
      focus: this.state.focus,
      blur: this.state.blur,
      error: this.state.error,
      valid: this.state.valid,
      onFocus: this.handleFocus,
      onBlur: this.handleBlur,
      value: props.value,
      ...InputProps,
    };

    const { reqValidators, reqErrorMessages } = required
      ? requiredValidator(intl) : { reqValidators: [], reqErrorMessages: [] };

    const validatorArr = typeof validators !== 'undefined'
      ? [...reqValidators, ...validators]
      : [...reqValidators];

    const errorMessagesArr = typeof errorMessages !== 'undefined'
      ? [...reqErrorMessages, ...errorMessages]
      : [...reqErrorMessages];

    return (
      <TextValidator
        inputRef={this.ref}
        autoFocus={autoFocus}
        fullWidth={fullWidth}
        data-testid={dataTestId}
        disabled={disabled}
        multiline={multiline}
        ref={this.ref}
        InputProps={{
          inputComponent: this.inputComponent,
          startAdornment: startAdornment ? <Adornment>{startAdornment}</Adornment> : null,
          endAdornment: this.buildEndAdornmnet(),
          ...DefaultInputProps,
        }}
        name={name}
        theme={theme}
        focus={this.state.focus}
        blur={this.state.blur}
        valid={this.state.valid}
        error={this.state.error}
        maxLength={maxLength}
        minLength={minLength}
        validators={validatorArr}
        errorMessages={errorMessagesArr}
        validatorListener={this.handleValidatorChange}
        data-variant={variant}
        required={required}
        {...props}
      />
    );
  }
}

BaseInput.propTypes = {
  ...inputPropTypes,
};

BaseInput.defaultProps = {
  autoFocus: false,
  readOnly: false,
  variant: InputVariants.DEFAULT,
};

export default injectIntl(withTheme(BaseInput));
