import React from 'react';
import PropTypes from 'prop-types';

import { colorToRgbString } from '@quintoandar/cozy-utils/helpers/colorToRgb';
import withTheme from '@quintoandar/cozy-utils/hocs/withTheme';
import ThemePropType from '../styles/Theme/proptypes';
import MuiDivider from './MuiDivider';
import Wrapper from './Wrapper';

const propTypes = {
  theme: ThemePropType,

  marginBottom: PropTypes.string, // this prop is deprecated, use spacing props instead
  marginLeft: PropTypes.string, // this prop is deprecated, use spacing props instead
  marginRight: PropTypes.string, // this prop is deprecated, use spacing props instead
  marginTop: PropTypes.string, // this prop is deprecated, use spacing props instead

  mb: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  ml: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  mr: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  mt: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),

  className: PropTypes.string,
  light: PropTypes.bool,
  vertical: PropTypes.bool,
  dashed: PropTypes.bool,
  height: PropTypes.string,
};

const defaultProps = {
  dashed: false,
  light: false,
  vertical: false,
  marginBottom: '0',
  marginLeft: '0',
  marginTop: '0',
  marginRight: '0',
  height: '100%',
};

export const Divider = ({
  marginBottom,
  marginLeft,
  marginRight,
  marginTop,
  mb,
  ml,
  mr,
  mt,
  height,
  dashed,
  light,
  theme,
  vertical,
  className,
  ...rest
}) => (
  <Wrapper
    className={className}
    mb={mb || marginBottom}
    ml={ml || marginLeft}
    mr={mr || marginRight}
    mt={mt || marginTop}
    vertical={vertical}
    {...rest}
  >
    <MuiDivider
      dashed={dashed}
      color={light ? colorToRgbString(theme.palette.common.white, 0.24) : theme.palette.grey.lighter}
      vertical={vertical}
      height={height}
    />
  </Wrapper>
);

Divider.propTypes = propTypes;
Divider.defaultProps = defaultProps;

export default withTheme(Divider);
