import React from 'react';
import PropTypes from 'prop-types';
import { FormattedMessage, injectIntl } from 'react-intl';

import withTheme from '@quintoandar/cozy-utils/hocs/withTheme';
import MessageDescriptorPropType from '../proptypes/Intl';
import Typography from '../Typography';
import ThemePropType from '../styles/Theme/proptypes';
import {
  LinkRelPropType, LinkTargetPropType, LinkVariantPropType, LinkRel, LinkTarget,
} from './proptypes';
import Link from './Link';
import messages from './messages';

export const CozyLink = ({ intl, message, icon: Icon, ...props }) => (
  <Link
    aria-label={props.target === LinkTarget.BLANK
      ? intl.formatMessage(messages.blank)
      : intl.formatMessage(messages.self)}
    {...props}
  >
    <Typography>
      <span className="styled-underline">
        <FormattedMessage {...message} />
        {Icon && <Icon />}
      </span>
    </Typography>
  </Link>
);

CozyLink.propTypes = {
  intl: PropTypes.object.isRequired,
  message: MessageDescriptorPropType.isRequired,
  icon: PropTypes.elementType,
  ariaLabel: PropTypes.string,
  to: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
  href: PropTypes.string,
  rel: LinkRelPropType,
  target: LinkTargetPropType,
  theme: ThemePropType,
  variant: LinkVariantPropType,
};

CozyLink.defaultProps = {
  rel: LinkRel.NOREFERRER,
};

export default withTheme(injectIntl(CozyLink));
