import { css } from 'styled-components';

import { colorToRgbString } from '@quintoandar/cozy-utils/helpers/colorToRgb';
import { ButtonVariants, IconButtonVariants } from '../proptypes';

const ButtonStyles = (theme) => ({
  [IconButtonVariants.PRIMARY]: css`
    color: ${theme.palette.primary.main};
    &:focus {
      box-shadow: none;
    }
  `,
  [IconButtonVariants.SECONDARY]: css`
    color: ${theme.palette.grey.highEmphasis};
    &:hover, &:focus {
      background-color: ${theme.palette.grey.base};
    }
  `,
  [IconButtonVariants.INVERSE]: css`
    color: ${theme.palette.common.white};
    &:hover, &:focus {
      background-color: ${colorToRgbString(theme.palette.primary.dark, 0.4)};
    }
  `,
  [IconButtonVariants.OFF]: css`
    background-color: ${theme.palette.common.white};
    border: solid 1px ${theme.palette.grey.lighter};
    color: ${theme.palette.grey.highEmphasis};
    svg {
      color: ${theme.palette.grey.highEmphasis};
    }
    &:hover, &:focus {
      background-color: ${theme.palette.grey.lighter};
    }
  `,
  [IconButtonVariants.ON]: css`
    background-color: ${theme.palette.primary.light};
    color: ${theme.palette.primary.dark};
    &:hover, &:focus {
      background-color: #D8DBFC;
    }
  `,
  // TERTIARY_OFF and TERTIARY_ON are deprecated and will be removed soon
  [ButtonVariants.TERTIARY_OFF]: css`
    background-color: ${theme.palette.common.white};
    border: solid 1px ${theme.palette.grey.lighter};
    color: ${theme.palette.grey.highEmphasis};
    svg {
      color: ${theme.palette.grey.highEmphasis};
    }
    &:hover, &:focus {
      background-color: ${theme.palette.grey.lighter};
    }
  `,
  [ButtonVariants.TERTIARY_ON]: css`
    background-color: ${theme.palette.primary.light};
    color: ${theme.palette.primary.dark};
    &:hover, &:focus {
      background-color: #D8DBFC;
    }
  `,
});

export default ButtonStyles;
