import PropTypes from 'prop-types';

const Placements = Object.freeze({
  TOP_START: 'top-start',
  TOP: 'top',
  TOP_END: 'top-end',
  LEFT_START: 'left-start',
  LEFT: 'left',
  LEFT_END: 'left-end',
  RIGHT_START: 'right-start',
  RIGHT: 'right',
  RIGHT_END: 'right-end',
  BOTTOM_START: 'bottom-start',
  BOTTOM: 'bottom',
  BOTTOM_END: 'bottom-end',
});

const PlacementPropType = PropTypes.oneOf(Object.values(Placements));

export {
  Placements,
};
export default PlacementPropType;
