import React from 'react';
import PropTypes from 'prop-types';
import { ThemeProvider } from 'styled-components';

import MuiThemeProvider from './ThemeProvider';
import createGenerateClassName from '../helpers/createGenerateClassName';
import StyleProvider from './StylesProvider';

const CozyProvider = ({ theme, children, generateClassNameOptions }) => (
  <StyleProvider generateClassName={createGenerateClassName(generateClassNameOptions)} injectFirst>
    <MuiThemeProvider theme={theme}>
      <ThemeProvider theme={theme}>
        { children }
      </ThemeProvider>
    </MuiThemeProvider>
  </StyleProvider>
);

CozyProvider.propTypes = {
  theme: PropTypes.object.isRequired,
  children: PropTypes.any,
  generateClassNameOptions: PropTypes.object,
};

CozyProvider.defaultProps = {
  generateClassNameOptions: {},
};

export default CozyProvider;
