import React from 'react';
import PropTypes from 'prop-types';

import withTheme from '@quintoandar/cozy-utils/hocs/withTheme';
import ThemePropType from '../styles/Theme/proptypes';
import {
  AppBarAlignments,
  AppBarAlignPropType,
  AppBarColors,
  AppBarColorPropType,
  AppBarPositions,
  AppBarPositionPropType,
} from './proptypes';

import Header from './Header';
import Toolbar from './Toolbar';
import AppBarTitle from './Title';
import AppBarNavigation from './Navigation';
import AppBarLink from './Link';


/**
 *
 * @name
 * AppBar
 *
 * @description
 * AppBar component
 *
 * @example
 * <AppBar>
 *  <button>Button 1</button>
 *  <button>Button 2</button>
 * </AppBar>
 *
 * @param {"transparent"|"white"} [color] Define the color of AppBar
 * @param {"relative"|"static"|"absolute"|"fixed"|"sticky"} [position] AppBar position
 * @param {bool} [underline] Define if AppBar will have a bottom line
 * @param {node} [outerChildren] Define components that you want below the main content of the AppBar. Example: Tabs
 * @param {string} [align] Define AppBar components alignment
 */
const AppBar = ({
  color,
  position,
  children,
  outerChildren,
  align,
  underline,
  theme,
  disableGutters,
}) => (
  <Header
    color={color}
    position={position}
    underline={underline}
    theme={theme}
  >
    <Toolbar align={align} underline={underline} disableGutters={disableGutters}>
      {children}
    </Toolbar>
    {outerChildren}
  </Header>
);

AppBar.propTypes = {
  theme: ThemePropType,
  children: PropTypes.any,
  outerChildren: PropTypes.any,
  underline: PropTypes.bool,
  color: AppBarColorPropType,
  position: AppBarPositionPropType,
  align: AppBarAlignPropType,
  disableGutters: PropTypes.bool,
};

AppBar.defaultProps = {
  underline: true,
  position: AppBarPositions.STATIC,
  align: AppBarAlignments.FLEX_START,
  disableGutters: false,
};

export {
  AppBar,
  AppBarTitle,
  AppBarNavigation,
  AppBarLink,
  AppBarColors,
  AppBarPositions,
  AppBarAlignments,
};

export default withTheme(AppBar);
