import { css } from 'styled-components';
import { fontSize } from '../Typography/variantType';

const InputStyles = (theme, disabled, error, valid, multiline) => ({
  default: css`
    padding: 0 ${theme.spacing()}px;
    height: 48px;
    border: 0;
    outline: 0;
    font-size: ${fontSize.body1};
    transition: 0.3s;
    color: ${theme.palette.common.highEmphasis};
    display: flex;
    align-items: center;

    input {
      padding-top: 0;
      padding-bottom: 0;
    }

    ${multiline && css`
      padding: ${theme.spacing()}px ${theme.spacing()}px ${theme.spacing(4)}px;
      min-height: 48px;
      height: auto;
    `}

    ${disabled && css`
      background: ${theme.palette.grey.lighter};
      color: ${theme.palette.grey.disabled};
    `}

    ${error && css`
      background: ${theme.palette.error.light};
    `}

    ${valid && css`
      background: ${theme.palette.success.light};
    `}

    ::placeholder {
      color: ${theme.palette.grey.disabled};
      opacity: 1;
    }
  `,
});

export default InputStyles;
