import React from 'react';
import styled from 'styled-components';

import withTheme from '@quintoandar/cozy-utils/hocs/withTheme';
import { ButtonVariants } from '../proptypes';

import ButtonBase, { propTypes } from '../ButtonBase';
import IconWrapper from '../IconWrapper';

export const FabBase = styled(ButtonBase)`
  box-shadow: ${({ theme }) => theme.shadows[3]};
`;

export const Button = ({ icon, children, ...rest }) => {
  const Icon = icon;

  return (
    <FabBase {...rest} color="primary">
      {icon && <IconWrapper><Icon /></IconWrapper>}
      {children}
    </FabBase>
  );
};

Button.propTypes = propTypes;

Button.defaultProps = {
  variant: ButtonVariants.PRIMARY,
  size: 'medium',
};

export default withTheme(Button);
