import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

import MuiButton from '@material-ui/core/Button';
import buttonStyles from './buttonStyles';
import ThemePropType from '../styles/Theme/proptypes';
import ButtonVariantPropType, { ButtonVariants } from './proptypes';

export const disabled = 'disabled';

export const propTypes = {
  theme: ThemePropType,
  icon: PropTypes.elementType,
  disabled: PropTypes.bool,
  fullWidth: PropTypes.bool,
  href: PropTypes.string,
  size: PropTypes.oneOf(['small', 'medium']),
  variant: ButtonVariantPropType,
  children: PropTypes.node,
};

// eslint-disable-next-line no-unused-vars
const ButtonBase = styled(({ variant, theme, ...rest }) =>
  (
    <MuiButton
      variant={variant === ButtonVariants.PRIMARY ? 'contained' : 'text'}
      {...rest}
    />
  )).attrs({ classes: { disabled } })`

    ${({ variant, theme }) => buttonStyles(theme)[variant]}
    min-width: 48px;
    height: 48px;
    padding: ${({ theme }) => `${theme.spacing(0.5)}px ${theme.spacing(2)}px`};
    border-radius: ${({ theme }) => theme.shape.borderRadius}px;
    box-shadow: ${({ theme }) => theme.shadows[0]};
    text-transform: none;
    cursor: pointer;

    &&.${disabled} {
      background-color: ${({ theme }) => theme.palette.grey.lighter};
      color: ${({ theme }) => theme.palette.grey.disabled};
      border: none;

      svg {
        color: ${({ theme }) => theme.palette.grey.disabled};
      }
    }

    ${({ size, theme }) => size === 'small' && `
      position: relative;
      margin: ${theme.spacing(0.5)}px 0;
      height: 40px;

      &:before, &:after {
        content: '';
        width: 100%;
        height: 4px;
        position: absolute;
      }

      &:before {
        top: -${theme.spacing(0.5)}px;
      }

      &:after {
        bottom: -${theme.spacing(0.5)}px;
      }
    `}
  `;

export default ButtonBase;
