import messages from './messages';

export const getDefaultErrors = (intl) => {
  const requiredError = intl.formatMessage(messages.requiredField);
  const invalidCharError = intl.formatMessage(messages.invalidChar);
  const invalidNumberError = intl.formatMessage(messages.invalidNumberError);
  return { requiredError, invalidCharError, invalidNumberError };
};

export default getDefaultErrors;
