import React from 'react';
import styled from 'styled-components';

import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import withTheme from '@quintoandar/cozy-utils/hocs/withTheme';
import InputStyles from './InputStyles';

export const TextArea = styled((props) => <TextareaAutosize {...props} />)`
  ${({
    theme, disabled, error, valid, multiline,
  }) => InputStyles(theme, disabled, error, valid, multiline).default}
`;

export default withTheme(TextArea);
