import PropTypes from 'prop-types';

const AppBarPositions = Object.freeze({
  STATIC: 'static',
  STICKY: 'sticky',
  ABSOLUTE: 'absolute',
  FIXED: 'fixed',
  RELATIVE: 'relative',
});

const AppBarAlignments = Object.freeze({
  FLEX_START: 'flex-start',
  FLEX_END: 'flex-end',
  CENTER: 'center',
  SPACE_BETWEEN: 'space-between',
  SPACE_AROUND: 'space-around',
});

const AppBarColors = Object.freeze({
  TRANSPARENT: 'transparent',
  WHITE: 'white',
});

const AppBarPositionPropType = PropTypes.oneOf(Object.values(AppBarPositions));
const AppBarAlignPropType = PropTypes.oneOf(Object.values(AppBarAlignments));
const AppBarColorPropType = PropTypes.oneOf(Object.values(AppBarColors));

export {
  AppBarAlignments,
  AppBarAlignPropType,
  AppBarColors,
  AppBarColorPropType,
  AppBarPositions,
  AppBarPositionPropType,
};
