import React from 'react';

import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';

import ThemePropType from '../styles/Theme/proptypes';
import { LinkVariants } from './proptypes';

// eslint-disable-next-line no-unused-vars
export const getLinkComponent = ({ component: Link, children, ...props }) =>
  Link
    ? <Link {...props}>{children}</Link>
    : <a {...props}>{children}</a>;

export const getVariantColor = ({ variant, theme: { palette } }) => variant
  === LinkVariants.INVERSE ? palette.grey.highEmphasis : palette.primary.main;

export const SelectedLink = styled(getLinkComponent)`
  text-decoration: none;
  color: ${getVariantColor};

  svg {
    width: 16px;
    height: 16px;

    &:first-child {
      margin-right: 4px;
    }

    &:last-child {
      margin-left: 4px;
    }
  }

  & > span {
    cursor: pointer;
    display: inline;
    padding-bottom: 4px;
    ${({ theme }) => css`
      background-image: linear-gradient(${theme.palette.primary.light}, ${theme.palette.primary.light});`}
    background-position: 0% 100%;
    background-repeat: no-repeat;
    background-size: 100% 1px;

    > span {
      padding-bottom: 4px;
      background-image: linear-gradient(${getVariantColor}, ${getVariantColor});
      background-position: 0% 100%;
      background-repeat: no-repeat;
      background-size: 0% 1px;
      transition: background-size .3s;

      &:hover, &:focus, &:active {
        background-size: 100% 1px;
      }
    }
  }

  &:visited {
    & > span  {
      > span {
        ${({ variant, theme }) => variant === LinkVariants.INVERSE ? css`
          color: ${theme.palette.common.black};` : css`
          color: ${theme.palette.primary.dark};`}
        }}
      }
    }
  }
`;

getLinkComponent.propTypes = {
  component: PropTypes.elementType,
  children: PropTypes.element,
  ariaLabel: PropTypes.string.isRequired,
  to: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
  href: PropTypes.string,
  theme: ThemePropType,
};

export default SelectedLink;
