import React from 'react';
import PropTypes from 'prop-types';
import { FormattedMessage, injectIntl } from 'react-intl';

import Close from '@material-ui/icons/Close';

import withTheme from '@quintoandar/cozy-utils/hocs/withTheme';
import Box from '../Box';
import MessageDescriptorPropType from '../proptypes/Intl';
import AlertColorPropType, { AlertColors } from './proptypes';
import Typography, { variantType } from '../Typography';
import ThemePropType from '../styles/Theme/proptypes';

import Link from '../Link';
import Wrapper from './Wrapper';
import CloseButtonWrapper from './CloseButtonWrapper';
import messages from './messages';
import { getIconColor } from './utils';

const Alert = ({
  intl,
  theme,
  color,
  title,
  children,
  icon,
  linkProps,
  className,
  closeMessage,
  onClose,
  hasMargin,
}) => {
  const Icon = icon;
  const iconColor = getIconColor(color, theme);

  return (
    <Box m={hasMargin ? 2 : 0}>
      <Wrapper theme={theme} color={color} className={className} hasMargin={hasMargin}>
        <Box p={2} display="flex">
          {
            icon || onClose ? (
              <Box>
                {
                  icon ? (
                    <Box mr={1}>
                      <Icon htmlColor={iconColor} />
                    </Box>
                  ) : undefined
                }
                {
                  onClose ? (
                    <CloseButtonWrapper aria-label={intl.formatMessage(closeMessage)}>
                      <Close htmlColor={iconColor} onClick={onClose} />
                    </CloseButtonWrapper>
                  ) : undefined
                }
              </Box>
            ) : undefined
          }
          <Box display="block">
            {
              title ? (
                <Typography
                  variant={variantType.body2}
                  gutterBottom={typeof children !== 'undefined' || typeof linkProps.message !== 'undefined'}
                >
                  <FormattedMessage {...title} />
                </Typography>
              ) : undefined
            }
            {
              children ? (
                <Typography color="textSecondary" gutterBottom={Boolean(linkProps.message)}>
                  {children}
                </Typography>
              ) : undefined
            }
            {
              linkProps.message ? (
                <Link {...linkProps} />
              ) : undefined
            }
          </Box>
        </Box>
      </Wrapper>
    </Box>
  );
};

Alert.propTypes = {
  className: PropTypes.string,
  icon: PropTypes.elementType,
  title: MessageDescriptorPropType,
  closeMessage: MessageDescriptorPropType,
  onClose: PropTypes.func,
  linkProps: PropTypes.shape({
    icon: PropTypes.elementType,
    component: PropTypes.elementType,
    message: MessageDescriptorPropType,
  }),
  color: AlertColorPropType,
  children: PropTypes.node,
  hasMargin: PropTypes.bool,
  intl: PropTypes.object.isRequired,
  theme: ThemePropType,
};

Alert.defaultProps = {
  hasMargin: true,
  color: AlertColors.GREY,
  closeMessage: messages.close,
  linkProps: { target: '_blank' },
};

export { Alert, AlertColors };
export default withTheme(injectIntl(Alert));
